import Vue from 'vue'
import VueRouter from 'vue-router'
import Makanan from '../src/components/Makanan.vue'
import Minuman from '../src/components/Minuman.vue'


Vue.use(VueRouter)

export default new VueRouter({
    routes: [
        {
            path: '/',
            name: 'makanan',
            component: Makanan
        },
        {
            path: '/about',
            name: 'minuman',
            component: Minuman
        },
    ]
})