<template>
  <v-container fluid>
    <v-data-iterator :items="desserts" :items-per-page.sync="itemsPerPage" hide-default-footer >
      <template v-slot:header>
        <v-toolbar class="mb-2" color="indigo darken-5" dark flat >
          <v-toolbar-title>CRUD Atma pedia</v-toolbar-title>
        </v-toolbar>
      </template>
      <template v-slot:default="props">

          <v-dialog v-model="dialog" max-width="500px">
            <template >
                
            </template>
            <template v-slot:activator="{ on }">  
                <v-btn color="primary" dark class="mb-2" v-on="on">buat makanan</v-btn>
                <v-btn color="red" dark class="mb-2 mr-2" v-show="hidden" @click="deleteSelected">Delete</v-btn>
            </template>
            <v-card>
                <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
                            </v-col>

                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
                            </v-col>

                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                            </v-col>

                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                            </v-col>

                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                    <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-row>
            <v-col v-for="item in props.items" :key="item.name" cols="12" sm="6" md="4" lg="3">
                <v-card>
                    <v-card-title class="subheading font-weight-bold">{{ item.name }}</v-card-title>

                    <v-divider></v-divider>

                    <v-list dense>
                        <v-list-item>
                        <v-list-item-content>Calories:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.calories }}</v-list-item-content>
                        </v-list-item>

                        <v-list-item>
                        <v-list-item-content>Fat:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.fat }}</v-list-item-content>
                        </v-list-item>

                        <v-list-item>
                        <v-list-item-content>Carbs:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.carbs }}</v-list-item-content>
                        </v-list-item>

                        <v-list-item>
                        <v-list-item-content>Protein:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.protein }}</v-list-item-content>
                        </v-list-item>
                    </v-list>

                    <v-divider></v-divider>

                    <v-card-actions>
                        <v-btn @click="editItem(item)" color="green" dark class="mb-2" v-on="on">Edit</v-btn>
                        <v-btn @click="deleteItem(item)" color="red" dark class="mb-2" v-on="on">Delete</v-btn>
                    </v-card-actions>
        
                </v-card>
          </v-col>
        </v-row>

        <v-col>
            <template v-slot:item.deleteItem="{ item }">
                <v-checkbox
                    v-model.number="selected"
                    color="red"
                    :value="item['.key']"
                ></v-checkbox>
            </template>

            <template v-slot:item.action="{ item }">
                <v-icon @click="editItem(item)" class="mr-1">mdi-glass-cocktail</v-icon>
                <v-icon @click="deleteItem(item)" class="mr-1">mdi-delete</v-icon>
            </template>
        </v-col>
      </template>
    </v-data-iterator>
  </v-container>
</template>

<script>
import { dessertsRef } from '../firebase';
    export default {
        data: () => ({
            i: 0,
            cek: -1,
            search: '',
            dialog: false,
            headers: [
                {
                    text: 'Dessert (100g serving)',
                    align: 'left',
                    sortable: false,
                    value: 'name',
                },
                {
                    text: 'Calories',
                    value: 'calories'
                },
                {
                    text: 'Fat (g)',
                    value: 'fat'
                },
                {
                    text: 'Carbs (g)',
                    value: 'carbs'
                },
                {
                    text: 'Protein (g)',
                    value: 'protein'
                },
                {
                    text: '#',
                    value: 'deleteItem',
                    sortable: false
                },
                {
                    text: 'Actions',
                    value: 'action',
                    sortable: false
                }
            ],
            hidden: false,
            selected: [],
            desserts: [],
            editedIndex: -1,
            editedItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
            defaultItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
        }),

        firebase: {
            desserts : dessertsRef
        },

        computed: {
            formTitle() {
                return this.cek === -1 ? 'New Item' : 'Edit Item'
            }
        },

        watch: {
            dialog(val) {
                val || this.close()
            },
            selected() {
                if (!this.selected[0]){
                    this.hidden = false
                }
                else {
                    this.hidden = true
                }
            }
        },

        methods: {
            close() {
                this.dialog = false
                setTimeout(() => {
                    this.editedItem = Object.assign({}, this.defaultItem)
                    this.editedIndex = -1
                }, 300)
            },
            
            save() {
                this.selected = []
                if (this.cek > -1) {
                    dessertsRef.child(this.editedIndex).set({name: this.editedItem.name,calories: this.editedItem.calories, fat : this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
                    this.clear()
                } else {
                    dessertsRef.push({name: this.editedItem.name,calories: this.editedItem.calories, fat : this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
                    this.clear()
                }
                this.close()
            },

            clear() {
                this.editedItem = {}
                this.editedItem.name = ''
                this.editedItem.calories = 0
                this.editedItem.fat = 0
                this.editedItem.carbs = 0
                this.editedItem.protein = 0
                this.cek = -1
            },

            editItem(item) {
                this.editedIndex = item['.key']
                this.cek = 0
                this.editedItem = Object.assign({}, item)
                this.dialog = true
            },

            deleteItem(item) {
                this.selected = []
                confirm("Are You Sure?") && dessertsRef.child((item['.key'])).remove()
            },

            deleteSelected() {
                for (this.i = 0; this.i < this.selected.length; this.i++) {
                    dessertsRef.child((this.selected[this.i])).remove()
                }
                this.i = 0
                this.selected = []
            }
        },
    }
</script>